<template>
    <div>
        <div v-if="loading" class="loader">
            <p>Loading...</p>
        </div>

        <div v-else>
            <div class="page-ref m-2">
                <RefBanner
                    class="page-ref__banner"
                    :link="link"
                    :refIncome="refIncome"
                    @copyRef="copyRef"
                    @take="take"
                />
                <RefInfo :data="refData" />
                <div class="page-ref__info-blocks">
                    <RefFriends class="page-ref__info-block" />
                    <RefBonus
                        class="page-ref__info-block"
                        :refIncome="refIncome"
                        :bonuses="bonusRewards"
                        @take="take"
                        @take-bonus="takeBonus"
                    />
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import RefBanner from "./RefBanner.vue";
import RefInfo from "./RefInfo.vue";
import RefFriends from "./RefFriends.vue";
import RefBonus from "./RefBonus.vue";
import axios from "axios";

export default {
    components: {
        RefBanner,
        RefInfo,
        RefFriends,
        RefBonus,
    },

    data() {
        return {
            loading: true,
            refIncome: "0.00",
            refReward: "0.00",
            link: null,
            refData: [],
            bonusRewards: [],
            error: false,
        };
    },

    methods: {
        async init() {
            this.loading = true;
            this.error = false;
            try {
                const response = await axios.post(`/referral/get`);
                const { data, ref_income, ref_reward, link, bonus_rewards } = response.data;

                this.refIncome = parseFloat(ref_income).toFixed(2);
                this.refReward = parseFloat(ref_reward).toFixed(2);
                this.link = link;
                this.refData = this.formatRefData(data);
                this.bonusRewards = bonus_rewards || [];
            } catch (error) {
                console.error("  :", error);
                this.$root.$emit("noty", {
                    title: "",
                    text: "   ",
                    type: "error",
                });
                this.error = true;
                this.refData = [];
            } finally {
                this.loading = false;
            }
        },

        formatRefData(data) {
            if (!data.lvl_1 || !data.lvl_2 || !data.lvl_3) {
                return [];
            }
            return [
                {
                    title: "",
                    class: "ref-info__col_passive",
                    data: ["1 ", "2 ", "3 "]
                },
                {
                    title: "",
                    data: [data.lvl_1.count, data.lvl_2.count, data.lvl_3.count]
                },
                {
                    title: "",
                    class: "ref-info__col_active",
                    data: [
                        `${data.lvl_1.income} `,
                        `${data.lvl_2.income} `,
                        `${data.lvl_3.income} `
                    ]
                },
                {
                    title: "",
                    data: ["10%", "3%", "2%"]
                }
            ];
        },

        async take() {
            try {
                const response = await axios.post(`/referral/take`);
                const { error, message, balance } = response.data;

                if (error) {
                    this.$root.$emit("noty", {
                        title: "",
                        text: message,
                        type: "error",
                    });
                    return;
                }

                this.refIncome = "0.00";

                this.$store.commit("setUser", {
                    ...this.$store.state.user,
                    balance: parseFloat(balance),
                });

                this.$root.$emit("noty", {
                    title: "",
                    text: "  !",
                    type: "success",
                });

                await this.init(); //    
            } catch (error) {
                console.error("   '':", error);
                this.$root.$emit("noty", {
                    title: "",
                    text: "   ",
                    type: "error",
                });
            }
        },

        async takeBonus() {
            try {
                this.loading = true;
                const response = await axios.post(`/referral/bonus/take`);
                if (response.data.success) {
                    this.$root.$emit("noty", {
                        title: "",
                        text: response.data.message || "  !",
                        type: "success",
                    });
                    await this.init(); //     
                } else {
                    this.$root.$emit("noty", {
                        title: " ",
                        text: response.data.message || "    ",
                        type: "info",
                    });
                }
            } catch (error) {
                this.$root.$emit("noty", {
                    title: "",
                    text: "    ",
                    type: "error",
                });
            } finally {
                this.loading = false;
            }
        },

        copyRef() {
            if (this.link) {
                this.$clipboard(this.link);
                this.$root.$emit("noty", {
                    title: "",
                    text: "    ",
                    type: "success",
                });
            } else {
                this.$root.$emit("noty", {
                    title: "",
                    text: "  ",
                    type: "error",
                });
            }
        },
    },

    mounted() {
        this.init();
    },
};
</script>

<style lang="scss" scoped>
.page-ref {
    display: flex;
    flex-direction: column;
    gap: 24px;
    .page-ref__info-blocks {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 24px;
        .page-ref__info-block {
            flex: 1;
            height: 100%;
        }
    }
}

@media (max-width: 1024px) {
    .page-ref {
        gap: 16px;
        .page-ref__info-blocks {
            grid-template-columns: 1fr;
            grid-template-rows: repeat(2, auto);
            gap: 16px;
            padding: 0 15px;
        }
    }
}
</style>
