x solid #ffffff40;\n            .ref-bonus__table-header-item {\n                font-family: Inter, sans-serif;\n                font-size: 20px;\n                font-weight: 500;\n                line-height: 24.2px;\n                text-align: center;\n                color: #fff;\n            }\n        }\n        .ref-bonus__table-body {\n            display: flex;\n            flex-direction: column;\n            .ref-bonus__table-body-row {\n                display: grid;\n                grid-template-columns: repeat(2, 1fr);\n                height: 52px;\n                &.ref-bonus__table-body-row_highlight {\n                    background: rgba(255,255,255,0.05);\n                    border-radius: 8px;\n                }\n                &.ref-bonus__table-body-row_middle {\n                    background: transparent;\n                    border-radius: 0;\n                }\n\n                .ref-bonus__table-body-item {\n                    display: flex;\n                    align-items: center;\n                    justify-content: center;\n                    color: #fff;\n                    font-family: Rubik, sans-serif;\n                    font-size: 16px;\n                    font-weight: 400;\n                    line-height: 18.35px;\n                }\n            }\n        }\n    }\n    .ref-banner__actions {\n        display: flex;\n        flex-direction: column;\n        max-width: 528px;\n        margin-top: 24px;\n        gap: 16px;\n        color: #fff;\n        .ref-banner__action {\n            display: flex;\n            flex-direction: column;\n            max-width: 100%;\n            .ref-banner__label {\n                font-family: Inter, sans-serif;\n                font-size: 16px;\n                font-weight: 500;\n                line-height: 19.36px;\n            }\n            .ref-banner__input-container {\n                display: flex;\n                align-items: center;\n                margin-top: 8px;\n 